.container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;

}

.item {
    display: flex;
    padding: 10px;
    align-self: center;
    align-content: center;
    padding-bottom: 10%;
    margin: auto;
}

.image {
    width: 80%;
    height: auto;
    margin: auto;
    align-content: center;
    padding-bottom: 10%;
}